import { _decorator, Component, macro, sp } from 'cc';
import { Node } from 'cc';
import { Num } from '../../core_tgx/framework/util/Num';
import { LanguageSprite } from 'db://assets/module_basic/scripts/language/LanguageSprite';
import { Vec3 } from 'cc';
import { Label } from 'cc';
import { Animation } from 'cc';
import { SpriteFrame } from 'cc';
import { Sprite } from 'cc';
import { wildbandito } from './cWildbandito';
import { tween } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('wildbanditoBigwin')
export class wildbanditoBigwin extends Component {
    @property({ type: Sprite, tooltip: "" })
    public man: Sprite = null!;
    @property({ type: Sprite, tooltip: "" })
    public bigwinBoard: Sprite = null!;
    @property({ type: [SpriteFrame], tooltip: "" })
    public mans: SpriteFrame[] = [];
    @property({ type: [SpriteFrame], tooltip: "" })
    public bigwinBoards: SpriteFrame[] = [];
    @property({ type: Node, tooltip: "" })
    public trim: Node = null!;
    @property({ type: Animation, tooltip: "" })
    public ambientL: Animation = null!;
    @property({ type: Animation, tooltip: "" })
    public ambientR: Animation = null!;
    @property({ type: Label, tooltip: "coin label" })
    public coinLabel: Label = null!;
    @property({ type: LanguageSprite, tooltip: "" })
    public bigWinBw: LanguageSprite = null!;
    @property({ type: Animation, tooltip: "" })
    public screenIn: Animation = null!;

    onLoad() {
    }
    protected start(): void {
        wildbandito.bigwin = this;
        this.init();
    }
    private init() {
        this.coin = 0;
        this.coinLabel.string = "0";
        this.node.active = false;
        this.bigWinBw.dataID = "bw";
        this.bigWinBw.node.scale = new Vec3(1, 1);
    }

    //大奖数字滚动动画
    private aimCoin: number = 0;
    private addcoin: number = 0;
    private addOnce: number = 0;
    private _playOverBn: boolean = true;
    public get playOverBn(): boolean {
        return this._playOverBn;
    }
    public set playOverBn(v: boolean) {
        this._playOverBn = v;
    }

    private _coin: number = 0;
    public get coin(): number {
        return this._coin;
    }
    public set coin(v: number) {
        this._coin = v;
    }

    bigwincoin(coin: number) {
        wildbandito.main.audio.playBigwin();
        this.node.active = true;
        this.coin = coin;
        this.screenIn.play();
        this.playBigWinCoin();
    }

    private playBigWinCoin() {
        let coin = this.coin;
        this.node.active = true;
        this.bigBn = false;
        this.superBn = false;
        this.megaBn = false;
        this.aimCoin = coin;
        this.addcoin = 0;
        this.addOnce = this.aimCoin / 100;
        this.playOverBn = false;
        this.coinLabel.string = Num.exchangeToThousands(wildbandito.exchangeRate, this.addcoin);
        this.schedule(this.coinOneChange, 0.05, macro.REPEAT_FOREVER);
        this.scheduleOnce(this.closeBigWin, 16);
    }

    private bigBn: boolean = false;
    private superBn: boolean = false;
    private megaBn: boolean = false;
    private coinOneChange() {
        this.addcoin += this.addOnce;
        if (this.addcoin >= this.aimCoin) {
            this.playBigWin(this.aimCoin);
            this.playCoinOver();
        } else {
            this.playBigWin(this.addcoin);
            this.coinLabel.string = Num.exchangeToThousands(wildbandito.exchangeRate, this.addcoin);
        }
    }

    private playCoinOver() {
        wildbandito.main.audio.playBigwinEnd();
        this.unschedule(this.coinOneChange);
        this.playOverBn = true;
        this.coinLabel.string = Num.exchangeToThousands(wildbandito.exchangeRate, this.aimCoin);
        this.unschedule(this.closeBigWin);
        this.scheduleOnce(this.closeBigWin, 5);
    }

    private playBigWin(coin: number) {
        if (coin >= wildbandito.betSum * 35 && this.megaBn == false) {
            this.playAnim_MegaWin();
            this.megaBn = true;
        } else if (coin >= wildbandito.betSum * 15 && coin < wildbandito.betSum * 35 && this.superBn == false) {
            this.playAnim_SuperWin();
            this.superBn = true;
        } else if (coin < wildbandito.betSum * 15 && this.bigBn == false) {
            this.playAnim_BigWin();
            this.bigBn = true;
        }
    }

    onFastBigWin(e: TouchEvent) {
        if (this.playOverBn) {
            this.closeBigWin();
        } else {
            this.playCoinOver();
        }
    }

    //大奖BigWin动画
    private playAnim_BigWin() {
        this.bigWinBw.dataID = "bw";
        this.ambientL.play();
        this.ambientR.play();
        this.trim.children[0].active = true;
        this.trim.children[1].active = false;
        this.trim.children[2].active = false;
        this.bigwinBoard.spriteFrame = this.bigwinBoards[0];
        this.man.node.scale = new Vec3(1, 1);
        this.man.spriteFrame = this.mans[0];
        tween(this.man.node)
            .to(20, { scale: new Vec3(1.5, 1.5) })
            .start();
    }

    //大奖SuperWin动画
    private playAnim_SuperWin(coin: number = 0) {
        this.bigWinBw.dataID = "mw";
        this.ambientL.play();
        this.ambientR.play();
        this.trim.children[0].active = false;
        this.trim.children[1].active = true;
        this.trim.children[2].active = false;
        this.bigwinBoard.spriteFrame = this.bigwinBoards[1];
        this.man.node.scale = new Vec3(1, 1);
        this.man.spriteFrame = this.mans[1];
        tween(this.man.node)
            .to(20, { scale: new Vec3(1.5, 1.5) })
            .start();
    }

    //大奖MegaWin动画
    private playAnim_MegaWin(coin: number = 0) {
        this.bigWinBw.dataID = "smw";
        this.ambientL.play();
        this.ambientR.play();
        this.trim.children[0].active = false;
        this.trim.children[1].active = false;
        this.trim.children[2].active = true;
        this.bigwinBoard.spriteFrame = this.bigwinBoards[2];
        this.man.node.scale = new Vec3(1, 1);
        this.man.spriteFrame = this.mans[2];
        tween(this.man.node)
            .to(20, { scale: new Vec3(1.5, 1.5) })
            .start();
    }

    closeBigWin() {
        this.unschedule(this.closeBigWin);
        wildbandito.main.audio.stopBigwinEnd();
        wildbandito.main.closeBigWin();
        if (this.node.active) {
            // wildbandito.main.audio.stopbigwinmain();
            this.node.active = false;
            this.init();
        }
    }
}