import { SpriteFrame } from 'cc';
import { Sprite } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { wildbanditoGun } from './wildbanditoGun';
import { wildbandito } from './cWildbandito';
import { Label } from 'cc';
import { Color } from 'cc';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { macro } from 'cc';
import { Prefab } from 'cc';
import { instantiate } from 'cc';
import { Num } from '../../core_tgx/framework/util/Num';
import { Animation } from 'cc';
import { labNumShow } from '../../module_basic/utils/labNumShow';
const { ccclass, property } = _decorator;

@ccclass('wildbanditoFreeEnd')
export class wildbanditoFreeEnd extends Component {
    @property({ type: Prefab, tooltip: "金币预制体" })
    public coinPb: Prefab = null!;
    @property({ type: Label, tooltip: "免费次数" })
    public count: Label = null!;
    @property({ type: Node, tooltip: "金额" })
    public coin: Node = null!;
    @property({ type: Node, tooltip: "入场光效" })
    public light: Node = null!;
    @property({ type: Node, tooltip: "" })
    public btn: Node = null!;

    onLoad() {

    }

    protected start(): void {

    }

    setParent(coin: number) {
        wildbandito.main.audio.playTotalwin();
        this.node.parent = wildbandito.slotCtrl.node;
        this.createCoins();
        this.coin.parent.scale = new Vec3(0.1, 0.1);
        let coinNum: labNumShow = this.coin.getComponent(labNumShow);
        coinNum.num = 0;
        this.light.active = true;
        this.light.opacity = 255;
        tween(this.light)
            .to(0.4, { opacity: 0 })
            .start();
        this.btn.active = false;
        tween(this.coin.parent)
            .delay(0.4)
            .call(() => {
                this.bindCoin(coin);
            })
            .to(1, { scale: new Vec3(1, 1) })
            .start();
    }

    private bindCoin(coin: number) {
        let coinNum: labNumShow = this.coin.getComponent(labNumShow);
        tween(coinNum)
            .to(4, { num: coin * 100 })
            .call(this.showBtn, this)
            .start();
    }

    private showBtn() {
        wildbandito.main.audio.playTotalwinEnd();
        this.btn.active = true;
        this.btn.scale = new Vec3(0.1, 0.1)
        tween(this.btn)
            .to(0.5, { scale: new Vec3(1.3, 1.3) })
            .start();
        this.scheduleOnce(this.close, 5);
    }

    private createCoins() {
        this.schedule(this.coinFactory, 0.2, macro.REPEAT_FOREVER);
    }

    /**金币工厂 自产自销*/
    private coinNum: number = 0;
    private coinFactory() {
        let node: Node = instantiate(this.coinPb);
        node.parent = this.node;
        this.coinNum++;
        node.setSiblingIndex(Math.random() > 0.5 ? this.coinNum + 1 : 0 + Math.floor(2 * Math.random()));
        let startPos: Vec3 = new Vec3();
        startPos.x = -500 + Math.random() * 1000;
        startPos.y = 1000;
        let endPos: Vec3 = new Vec3();
        endPos.x = startPos.x
        endPos.y = -1000;
        node.position = startPos;
        tween(node)
            .to(5, { position: endPos })
            .call(() => {
                node.parent = null;
                this.coinNum--;
            })
            .start();
    }

    private close() {
        this.node.parent = null;
        wildbandito.main.bg.freeTimes = -1;
        wildbandito.main.bg.fortune = false;
        wildbandito.main.bg.gun.curNum = 1;
        wildbandito.main.playRollOver();
    }

    private onClose() {
        if (this.btn.active) {
            this.close();
            wildbandito.main.audio.playFswonIncrease();
            wildbandito.main.audio.stopTotalwinEnd();
        }
    }
}