import { _decorator, Component, Node } from 'cc';
import { GameMgr } from '../../module_basic/scripts/GameMgr';
import { SubGameMgr } from '../../module_basic/scripts/SubGameMgr';
import { SubGameDef } from '../../module_basic/scripts/SubGameDef';
import { RoomMgr } from '../../module_basic/scripts/RoomMgr';
import { ModuleDef } from '../../scripts/ModuleDef';
import { ResourceMgr } from '../../core_tgx/base/ResourceMgr';
import { Prefab } from 'cc';
import { Logger } from '../../core_tgx/base/log/Logger';
const { ccclass, property } = _decorator;


@ccclass('SlotsWildbanditoGameMgr')
export class SlotsWildbanditoGameMgr extends GameMgr {
    private static _inst: SlotsWildbanditoGameMgr;
    public static get inst(): SlotsWildbanditoGameMgr {
        if (!this._inst) {
            this._inst = new SlotsWildbanditoGameMgr();
        }
        return this._inst;
    }

    constructor() {
        super();
        this._gameType = SubGameDef.SLOTS_WILDBANDITO;
        this._loadingScene = { name: 'subgame_loading', bundle: ModuleDef.BASIC };
        this._gameScene = { name: 'game_slots_wildbandito', bundle: ModuleDef.SLOTS_WILDBANDITO };
        this.initNetMsgHandlers();
    }
    initNetMsgHandlers() {

    }

    initSoloMode() {
        RoomMgr.inst.reset();
        this.reset();
        SubGameMgr.gameMgr = this;
    }

    /**
            * 加载资源
            */
    public override async preLoadRes(porcessFun: Function): Promise<boolean> {
        return new Promise((resolve, reject) => {
            this.preLoad(() => {
                resolve(true);
            }, porcessFun);
            return true;
        });
    }
    private totalNum: number = 2;
    private nowIndex: number = 0;
    private _porcessFun: Function;
    private modName: string = ModuleDef.SLOTS_WILDBANDITO;
    private mainName: string = "wildbanditoMain";
    public async preLoad(callback: Function, porcessFun: Function) {
        this.nowIndex = 0;
        this._porcessFun = porcessFun;
        if (porcessFun) porcessFun(this.nowIndex / this.totalNum);
        await this.preLoadMain(); //1
        this.finishOneItemLoad();
        await this.preLoadLanguage(); //2
        this.finishOneItemLoad();
        callback();
    }

    /**加载主场景预制体 */
    public preLoadMain(): Promise<void> {
        return new Promise((resolve, reject) => {
            let url: string = "prefab/" + this.mainName;
            ResourceMgr.inst.loadResByModuleBundle(this.modName, url, Prefab, (error: Error, loadedResource) => {
                if (error) {
                    Logger.error('载入Prefab失败, 原因:' + url);
                    return;
                }
                if (!(loadedResource instanceof Prefab)) {
                    Logger.error(this, '你载入的不是Prefab, 你做了什么事?');
                    return;
                }
                resolve();
            });
        })
    }
    /**加载语言包 */
    public preLoadLanguage(): Promise<void> {
        return new Promise((resolve, reject) => {
            resolve();
        })
    }
    private finishOneItemLoad() {
        this.nowIndex++;
        if (this._porcessFun) {
            this._porcessFun(this.nowIndex / this.totalNum, Math.floor(this.nowIndex / this.totalNum * 100) + "%");
        }
    }
}

SubGameMgr.inst.registerGameMgr(SlotsWildbanditoGameMgr.inst);


