/*
histroy
*/
import { _decorator, Component, Prefab, Node, ScrollView } from 'cc';
import { wildbanditoAudio } from './wildbanditoAudio';
import { wildbanditoSlotCtrl } from './wildbanditoSlotCtrl';
import { wildbandito } from './cWildbandito';
import { wildbanditoSymbol } from './wildbanditoSymbol';
import { tween } from 'cc';
import { Label } from 'cc';
import { Vec3 } from 'cc';
import { wildbanditoBg } from './wildbanditoBg';
import { wildbanditoFreeStart } from './wildbanditoFreeStart';
import { instantiate } from 'cc';
import { wildbanditoFreeEnd } from './wildbanditoFreeEnd';
import { RoomMgr } from '../../module_basic/scripts/RoomMgr';
import { UIHelp, UIhelpParam } from '../../module_basic/ui_help/UIHelp';
import { ApiReturn } from 'tsrpc-browser';
import { ResPlayMatch3 } from '../../module_basic/shared/protocols/public/slots/PtlPlayMatch3';
import { gameNet } from '../../module_basic/scripts/NetGameServer';
import { SubGameDef } from '../../module_basic/scripts/SubGameDef';
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';
import { addInfo, dataChange, Round } from './dataChange';
import { baseSymbol } from '../../module_basic/ui_slot/match3/baseSymbol';
import { reelDate } from '../../module_basic/ui_slot/match3/baseReel';
import { wildbanditoBigwin } from './wildbanditoBigwin';
import { wildbanditoDetail } from './wildbanditoDetail';
import { Button } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('wildbanditoMain')
export class wildbanditoMain extends Component {
    @property({ type: Prefab, tooltip: "免费开始" })
    public freeStart: Prefab = null!;
    @property({ type: Prefab, tooltip: "免费结束" })
    public freeEnd: Prefab = null!;
    @property({ type: Prefab, tooltip: "" })
    public detailPb: Prefab = null!;
    @property({ type: wildbanditoBg, tooltip: "背景" })
    public bg: wildbanditoBg = null!;
    @property({ type: Node, tooltip: "" })
    public slotCtrlNode: Node = null!;
    public audio: wildbanditoAudio = null!;
    public delayClick: boolean = false;

    onLoad() {
        wildbandito.main = this;
        this.audio = this.node.getComponent('wildbanditoAudio') as wildbanditoAudio;

    }

    start() {
        this.bg.fortune = false;
        wildbandito.infoBoard.lev = 0;
        wildbandito.infoBoard.showInfosBn = true;
    }

    onCLick(event: TouchEvent, args: any) {
        switch (args) {
            case "roll":
                this.startRoll();
                break;
            case "add":
                this.add();
                break;
            case "dec":
                this.dec();
                break;
            case "help":
                tgx.UIMgr.inst.showUI(UIHelp, null, this, new UIhelpParam("rule"));
                break;
            case "pay":
                tgx.UIMgr.inst.showUI(UIHelp, null, this, new UIhelpParam("payout"));
                break;
            case "lastmoney":
                wildbandito.lastmoneyPanel.showPanel();
                break;
            case "exitGame":
                this.onBtnExitClicked();
                break;
        }
    }

    onBtnExitClicked() {
        tgx.UIAlert.show(LanguageData.getLangByID("exit_game"), true).onClick(async (ok: Boolean) => {
            if (ok) {
                wildbandito.main.close();
                RoomMgr.inst.exitRoom();
            }
        });
    }

    startRoll() {
        if (this.delayClick || wildbandito.rollEnable == false) {
            return;
        }
        if (!wildbandito.auto) {
            this.delayClick = true;
            this.scheduleOnce(() => {
                this.delayClick = false;
            }, 1);
            if (wildbandito.status == 0) {
                wildbandito.status = 2;
                this.sendRoll();
            } else if (wildbandito.status == 1) {
                wildbandito.slotCtrl.setSpinAnim(3);
                this.stopImmediately(wildbandito.result.rounds[0].symbols, () => { this.rollCallBack() });
            }
        }
    }

    add() {
        if (wildbandito.auto) {
            return;
        }
        wildbandito.sumIdx += 1;
        wildbandito.sumIdx = wildbandito.slotCtrl.updateBet(wildbandito.sumIdx) == 0 ? wildbandito.sumIdx - 1 : wildbandito.sumIdx;
        tween(wildbandito.slotCtrl.lblCurBet.node)
            .to(0.1, { scale: new Vec3(1.2, 1.2, 1.2) })
            .to(0.1, { scale: new Vec3(1, 1, 1) })
            .start()
    }

    dec() {
        if (wildbandito.auto) {
            return;
        }
        wildbandito.sumIdx -= 1;
        wildbandito.sumIdx = wildbandito.slotCtrl.updateBet(wildbandito.sumIdx) == 0 ? wildbandito.sumIdx + 1 : wildbandito.sumIdx;
        tween(wildbandito.slotCtrl.lblCurBet.node)
            .to(0.1, { scale: new Vec3(1.2, 1.2, 1.2) })
            .to(0.1, { scale: new Vec3(1, 1, 1) })
            .start()
    }

    startAuto(n: any) {
        if (this.delayClick) {
            return;
        }
        if (wildbandito.status == 0) {
            wildbandito.auto = true;
            wildbandito.autoTimes = n;
            (((wildbandito.slotCtrl.Btn_stopAuto as Node).children[0] as Node).getComponent(Label) as Label).string = n + "";
            // (((wildbandito.slotCtrl.Btn_stopAuto as Node).children[1] as Node).getComponent(Label) as Label).string = n + "";
            if (n > 999) {
                ((wildbandito.slotCtrl.Btn_stopAuto as Node).children[0] as Node).scale = new Vec3(0.76, 0.76, 0.76);
                ((wildbandito.slotCtrl.Btn_stopAuto as Node).children[1] as Node).scale = new Vec3(0.76, 0.76, 0.76);
            } else if (n > 99) {
                ((wildbandito.slotCtrl.Btn_stopAuto as Node).children[0] as Node).scale = new Vec3(0.9, 0.9, 0.9);
                ((wildbandito.slotCtrl.Btn_stopAuto as Node).children[1] as Node).scale = new Vec3(0.9, 0.9, 0.9);
            }
            else {
                ((wildbandito.slotCtrl.Btn_stopAuto as Node).children[0] as Node).scale = new Vec3(1, 1, 1);
                ((wildbandito.slotCtrl.Btn_stopAuto as Node).children[1] as Node).scale = new Vec3(1, 1, 1);
            }
            wildbandito.slotCtrl.Btn_stopAuto.active = wildbandito.auto;
            wildbandito.slotCtrl.Btn_start.active = !wildbandito.slotCtrl.Btn_stopAuto.active;
            this.scheduleOnce(this.sendRoll, 1.5);
        }
    }

    stopAuto() {
        wildbandito.auto = false;
        wildbandito.autoTimes = 0;
    }

    /**滚轮回调，所有滚轮滚动完毕触发 */
    private rollCallBackBn: boolean = false;
    rollCallBack() {
        if (this.rollCallBackBn) {
            return;
        }
        wildbandito.status = 0;
        this.rollCallBackBn = true;
        this.scheduleOnce(() => {
            this.rollCallBackBn = false;
        }, 1)
        wildbandito.reel.removeRadom();
        if (wildbandito.result.winCoin > 0) {
            wildbandito.slotCtrl.setSpinAnim(3);
        } else {
            wildbandito.slotCtrl.setSpinAnim(2);
        }
        this.scheduleOnce(() => {
            let arr: wildbanditoSymbol[] = wildbandito.reel.getSymbols as wildbanditoSymbol[];
            for (let i = 0; i < arr.length; i++) {
                arr[i].spawn();
            }
            this.roundShow();
        }, 1)
    }

    private roundShow() {
        let round: Round = wildbandito.round;
        if (round.removeItems.length > 0) {
            this.showRemove();
        } else {
            this.settlement();
        }
    }

    private showRemove() {
        let remove: number[] = wildbandito.round.removeItems;
        if (remove.length > 1) {
            let arr: wildbanditoSymbol[] = wildbandito.reel.getSymbols as wildbanditoSymbol[];
            for (let i = 0; i < remove.length; i++) {
                let index: number = remove[i];
                arr[index].playWin();
            }
            if (wildbandito.round.winCoin > 0) {
                wildbandito.infoBoard.playAnimWin(wildbandito.round.winCoin, false);
            }
            this.scheduleOnce(this.doRemove, 3);
        } else {
            this.settlement();
        }
    }

    private doRemove() {
        this.bg.gun.curNum = wildbandito.round.mul;
        wildbandito.reel.drop();
    }

    dropBack() {
        this.doNext();
    }

    private settlement() {
        this.roundSM();
    }

    private roundSM() {
        if (wildbandito.round.winCoin > 0) {
            wildbandito.infoBoard.playAnimWin(wildbandito.round.winCoin, false);
            this.scheduleOnce(this.roundSMNext, 3);
        } else {
            this.roundSMNext();
        }
    }

    private roundSMNext() {
        if (wildbandito.roundIndex >= wildbandito.result.rounds.length - 1) {
            if (wildbandito.result.winCoin > 0) {
                if (wildbandito.result.winCoin < 5 * wildbandito.betSum) {
                    if (wildbandito.result.winCoin <= 3 * wildbandito.betSum) {
                        wildbandito.main.audio.playPrizeSmallwin();
                    } else {
                        wildbandito.main.audio.playPrizeMedwinCounting();
                    }
                    wildbandito.infoBoard.playAnimWin(wildbandito.round.winCoin, true);
                    this.scheduleOnce(this.doRoundSMNext, 2);
                } else {
                    wildbandito.bigwin.bigwincoin(wildbandito.result.winCoin);
                }
            } else {
                this.doRoundSMNext();
            }
        }
    }

    private doRoundSMNext() {
        if (wildbandito.resultIndex == 0 && wildbandito.roundIndex == wildbandito.result.rounds.length - 1 && wildbandito.lotteryRes.results.length > 1) {
            let freeStart: wildbanditoFreeStart = instantiate(this.freeStart).getComponent(wildbanditoFreeStart);
            freeStart.setParent();
        } else {
            if (wildbandito.round.addItems.length == 0) {
                this.doNext();
            }
        }
    }

    freeStartBack() {
        this.doNext();
    }

    private doNext() {
        if (wildbandito.roundIndex < wildbandito.result.rounds.length - 1) {
            this.doNextRound();
        } else {
            this.doNextResult();
        }
    }

    //继续下回合表现
    private doNextRound() {
        wildbandito.roundIndex++;
        this.roundShow();
    }

    //当前次数回合数据表现完毕，进入下一次摇奖表现
    private doNextResult() {
        wildbandito.infoBoard.showInfosBn = true;
        wildbandito.roundIndex = 0;
        wildbandito.resultIndex++;
        if (wildbandito.playOverBn) {
            if (wildbandito.lotteryRes.results.length == 1) {
                this.playRollOver();
            } else {
                let free: wildbanditoFreeEnd = instantiate(this.freeEnd).getComponent(wildbanditoFreeEnd);
                free.setParent(wildbandito.lotteryRes.winCoin);
            }
            return;
        }
        wildbandito.main.bg.freeTimes = wildbandito.result.leftFreeCount - 1;
        wildbandito.reel.loop();
        let delay: number = 0;
        if (wildbandito.showFreeLoopAni) {
            delay = 3;
        } else {
            delay = 1;
        }
        this.scheduleOnce(() => {
            wildbandito.reel.loopEnd(wildbandito.round.symbols, wildbandito.round.golds, () => {
                this.rollCallBack();
            });
        }, delay)
    }

    closeBigWin() {
        if (wildbandito.resultIndex == 0 && wildbandito.roundIndex == wildbandito.result.rounds.length - 1 && wildbandito.lotteryRes.results.length > 1) {
            let freeStart: wildbanditoFreeStart = instantiate(this.freeStart).getComponent(wildbanditoFreeStart);
            freeStart.setParent();
        } else {
            this.doNext();
        }
    }

    /**单次滚动完成结算 */
    playRollOver() {
        this.rollComplete();
        this.doAuto();
    }

    doAuto() {
        if (wildbandito.auto && wildbandito.autoTimes > 0) {
            this.sendRoll();
        }
    }
    /**滚动完成 */
    private rollComplete() {
        wildbandito.status = 0;
        wildbandito.slotCtrl.setSpinAnim(0);
        this.addDetailListener();
    }

    roll(list: number[], golds: number[]) {
        if (!wildbandito.auto) {
            wildbandito.rollEnable = true;
        }
        wildbandito.status = 1;
        this.scheduleOnce(() => {
            wildbandito.reel.loopEnd(list, golds, () => { this.rollCallBack() });
        }, 0.5);
    }



    sendRoll() {
        this.initRoll();
        wildbandito.autoTimes = wildbandito.autoTimes > 0 ? wildbandito.autoTimes - 1 : 0;
        if (wildbandito.autoTimes == 0) {
            wildbandito.auto = false;
        }
        wildbandito.slotCtrl.setSpinAnim(1);
        let autoTimesLab: Label = wildbandito.slotCtrl.Btn_stopAuto.children[0].getComponent(Label) as Label;
        autoTimesLab.string = wildbandito.autoTimes + "";
        // this.audio.playclickstart();
        if (wildbandito.autoTimes > 999) {
            wildbandito.slotCtrl.Btn_stopAuto.children[0].scale = new Vec3(0.5, 0.5);
        } else if (wildbandito.autoTimes > 99) {
            wildbandito.slotCtrl.Btn_stopAuto.children[0].scale = new Vec3(0.64, 0.64);
        }
        else {
            wildbandito.slotCtrl.Btn_stopAuto.children[0].scale = new Vec3(1, 1);
        }
        wildbandito.reel.loop();
        this.sendLottery();
    }

    firstSymbolOver() {
        wildbandito.reel.spawn();
        this.addDetailListener();
    }

    private addDetailListener() {
        let symbols: baseSymbol[] = wildbandito.reel.getSymbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: baseSymbol = symbols[i];
            symbol.node.getComponent(Button).interactable = true;
        }
    }

    private removeDetailListener() {
        let symbols: baseSymbol[] = wildbandito.reel.getSymbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: baseSymbol = symbols[i];
            symbol.darkBn = false;
            symbol.node.getComponent(Button).interactable = false;
        }
        let detail: wildbanditoDetail = wildbandito.detail;
        if (detail) detail.node.active = false;
    }

    addDetail(s: baseSymbol) {
        if (!wildbandito.rollEnable) return;
        let detail: wildbanditoDetail = wildbandito.detail;
        if (!detail) {
            detail = instantiate(this.detailPb).getComponent(wildbanditoDetail);
            detail.node.parent = this.node;
            wildbandito.detail = detail;
        }
        detail.node.active = true;
        let symbols: baseSymbol[] = wildbandito.reel.getSymbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: baseSymbol = symbols[i];
            if (symbol.poolId != s.poolId) {
                symbol.darkBn = true;
            }
        }
        detail.node.position = s.node.position;
        detail.node.y += wildbandito.reel.node.parent.parent.y;
        detail.init(s as wildbanditoSymbol);
    }

    /**每次滚动初始化场景 */
    initRoll() {
        this.removeDetailListener();
        wildbandito.infoBoard.showInfosBn = true;
    }

    async sendLottery() {
        let retSpin: ApiReturn<ResPlayMatch3> = await gameNet.PlayMatch3(SubGameDef.SLOTS_WILDBANDITO, wildbandito.betSum + "");
        this.lotteryBack(retSpin);
    }

    private lotteryBack(data: any) {
        if (data && data.isSucc) {
            console.log(data.res);
            this.exchange(data.res as ResPlayMatch3);
            console.log(wildbandito.lotteryRes);
            wildbandito.resultIndex = 0;
            wildbandito.roundIndex = 0;
            if (wildbandito.slotCtrl.isSpeed) {
                this.roll(wildbandito.round.symbols, wildbandito.round.golds)
            } else {
                this.scheduleOnce(() => {
                    this.roll(wildbandito.round.symbols, wildbandito.round.golds)
                }, 1)
            }
        }
    }

    exchange(data: ResPlayMatch3) {
        let res = dataChange.change(data);
        wildbandito.lotteryRes = res;
    }

    stopImmediately(arr: number[], callBack: Function = () => { }) {
        if (!wildbandito.auto) {
            wildbandito.reel.stopImmediately(arr, callBack)
        }
    }

    close() {

    }
}