import { Label } from 'cc';
import { Node } from 'cc';
import { _decorator, Component } from 'cc';
import { Prefab } from 'cc';

const { ccclass, property } = _decorator;

@ccclass('wildbanditoGunNum')
export class wildbanditoGunNum extends Component {
    numId: number = 0;
    gunScroll: Node = null!;
    /**偏移角度，提早60度做数字变更 */
    private offsetA: number = 60;
    /**位置半径 */
    private R: number = 160;
    private _curAngle: number = 0;
    public get curAngle(): number {
        return this._curAngle
    }
    public set curAngle(v: number) {
        this._curAngle = v;
        this.node.x = Math.cos(this.changeRadian(v)) * this.R;
        this.node.y = Math.sin(this.changeRadian(v)) * this.R;
        this.num = Math.floor((v + this.offsetA) / 360) * 6 + this.numId;
    }
    /**
     *              1(90)
     *      0（150）         2（30）
     * 
     *      5（-150）        3（-30）
     *              4（-90）
     */
    private getAngle(): number {
        return 30 * (5 - 2 * this.numId) + this.gunScroll.angle;
    }

    /**角度转弧度 */
    private changeRadian(v: number): number {
        return v / 180 * Math.PI;
    }

    onLoad() {

    }

    protected start(): void {

    }

    private get label(): Label {
        return this.node.getComponent(Label);
    }

    private _num: number = 0;
    public get num(): number {
        return this._num;
    }
    public set num(v: number) {
        this._num = v;
        if (v) {
            this.label.string = "x" + v;
        } else {
            this.label.string = "∞";
        }
    }

    protected update(dt: number): void {
        if (!this.gunScroll) return;
        this.curAngle = this.getAngle();
    }
}