import { _decorator, Component, Enum, Node, sp, Sprite, SpriteFrame } from 'cc';
import { instantiate } from 'cc';
import { Prefab } from 'cc';
import { tween } from 'cc';
import { Vec3 } from 'cc';
import { Animation } from 'cc';
import { baseSymbol, ESHOWTYPE } from '../../module_basic/ui_slot/match3/baseSymbol';
import { wildbandito } from './cWildbandito';


const { ccclass, property } = _decorator;

@ccclass('wildbanditoSymbol')
export class wildbanditoSymbol extends baseSymbol {
    @property({ type: Prefab, tooltip: "爆炸消失动画" })
    public boom: Prefab = null!;
    @property({ type: Prefab, tooltip: "爆炸消失动画" })
    public wildboom: Prefab = null!;
    @property({ type: Prefab, tooltip: "枪刷光" })
    public gunLight: Prefab = null!;
    @property({ type: Node, tooltip: "选中态" })
    public select: Node = null!;
    @property({ type: Node, tooltip: "金框符号" })
    public gold: Node = null!;

    onLoad() {

    }
    unuse(): void {
        this.goldBn = false;
        this.selectBn = false;
        super.unuse();
    }

    /**是否显示金框符号*/
    private _goldBn: boolean = false;
    public get goldBn(): boolean {
        return this._goldBn
    }
    public set goldBn(v: boolean) {
        this._goldBn = v;
        this.gold.active = v;
    }

    private _selectBn: boolean = false;
    public get selectBn(): boolean {
        return this._selectBn;
    }
    public set selectBn(v: boolean) {
        this._selectBn = v;
        this.select.active = v;
    }

    /**消除行为 */
    playBoom() {
        if (this.selectBn == false) return;
        let ani: Animation;
        if (this.changeBn()) {
            this.goldBn = false;
            this.symbolId = ESYMBOLID.wild;
            this.showType = ESHOWTYPE.ske;
            this.playAni(ESYMBOLANI.spawn, false);
            this.addAni(ESYMBOLANI.idle, true);
            ani = instantiate(this.wildboom).getComponent(Animation);
            ani.node.parent = this.node.parent.parent;
            ani.node.x = this.node.parent.x;
            ani.node.y = this.node.y;
            let gunAni: Animation = instantiate(this.gunLight).getComponent(Animation);
            gunAni.node.parent = this.node.parent.parent;
            gunAni.node.x = this.node.parent.x;
            gunAni.node.y = this.node.y;
            gunAni.on("finished" as any, () => {
                ani.node.parent = null;
                gunAni.node.parent = null;
            });
        } else {
            ani = instantiate(this.boom).getComponent(Animation);
            ani.node.parent = this.node.parent;
            ani.node.x = this.node.x;
            ani.node.y = this.node.y;
            ani.on("finished" as any, () => {
                this.recover();
                wildbandito.removeCount++;
                if (wildbandito.removeCount >= wildbandito.round.addItems.length)
                    this.scheduleOnce(() => {
                        wildbandito.reel.doAdd();
                    }, 0.1);
            }, this);
        }
        ani.play();
    }

    /**判断是否可以变更为wild */
    private changeBn(): boolean {
        return this.goldBn && this.symbolId != ESYMBOLID.wild;
    }

    /**滚动停止调用 播放wild出场动画*/
    spawn() {
        if (this.symbolId != ESYMBOLID.wild && this.symbolId != ESYMBOLID.scatter) return;
        this.showType = ESHOWTYPE.ske;
        this.playAni(ESYMBOLANI.spawn, false);
        this.addAni(ESYMBOLANI.idle, true);
        if (this.symbolId == ESYMBOLID.wild) {
            wildbandito.main.audio.playSpawnWild();
        }
        if (this.symbolId == ESYMBOLID.scatter) {
            wildbandito.main.audio.playSpawnScatter();
        }
    }
    /**播放中奖 */
    playWin() {
        this.showType = ESHOWTYPE.ske;
        this.playAni(ESYMBOLANI.win, true);
        this.selectBn = true;
    }
    onClick(e: TouchEvent) {
        wildbandito.main.addDetail(this);
    }
}

export enum ESYMBOLID {
    null,
    ten,
    J,
    Q,
    K,
    A,
    maraca,
    tea,
    guitar,
    man,
    wild,
    scatter
}

export enum ESYMBOLANI {
    win = "win",                  //中奖过场
    idle = "idle",                //中奖状态待机
    spawn = "spawn",              //wild 入场动画
}


