import { _decorator, Component, Enum, Node, sp, Sprite, SpriteFrame } from 'cc';
import { instantiate } from 'cc';
import { Prefab } from 'cc';
import { tween } from 'cc';
import { Vec3 } from 'cc';
import { Animation } from 'cc';
import { wildbanditoGunNum } from './wildbanditoGunNum';
import { wildbandito } from './cWildbandito';


const { ccclass, property } = _decorator;

@ccclass('wildbanditoGun')
export class wildbanditoGun extends Component {
    @property({ type: Prefab, tooltip: "子弹数字" })
    public gunNum: Prefab = null!;
    @property({ type: Sprite, tooltip: "滚轮光效" })
    public light: Sprite = null!;
    @property({ type: [SpriteFrame], tooltip: "滚轮光效" })
    public lights: SpriteFrame[] = [];
    @property({ type: Sprite, tooltip: "滚轮" })
    public gunScroll: Sprite = null!;
    @property({ type: [SpriteFrame], tooltip: "滚轮" })
    public gunScrolls: SpriteFrame[] = [];
    
    private oneAngle:number = 60;

    onLoad() {
        
    }

    protected start(): void {
        this.createGunNum();
    }

    /**模糊态 */
    private _blurBn:boolean = false; 
    public get blurBn() : boolean {
        return this._blurBn
    }
    public set blurBn(v : boolean) {
        if(this.fortune ||  this._blurBn == v) return;
        this._blurBn = v;
        if(v){
            this.gunScroll.spriteFrame = this.gunScrolls[0];
            this.gunScroll.node.scale = new Vec3(1.75,1.75);
        }else{
            this.gunScroll.spriteFrame = this.gunScrolls[1];
            this.gunScroll.node.scale = new Vec3(1,1);
        }
    }
    
    /**辛运模式 */
    private _fortune:boolean = false;
    public get fortune() : boolean {
        return this._fortune;
    }
    public set fortune(v : boolean) {
        this._fortune = v;
        if(v){
            this.gunScroll.spriteFrame = this.gunScrolls[2];
            this.light.spriteFrame = this.lights[1];
        }else{
            this.gunScroll.spriteFrame = this.gunScrolls[1];
            this.light.spriteFrame = this.lights[0];
        }
    
    }  

    /**滚轮数字 */
    private gunNums:wildbanditoGunNum[] = [];
    private gunNumsLen:number = 6;
    private createGunNum(){
        for(let i=0;i<this.gunNumsLen;i++){
            let gunNum:wildbanditoGunNum = instantiate(this.gunNum).getComponent(wildbanditoGunNum);
            gunNum.numId = i;
            gunNum.gunScroll = this.gunScroll.node;
            gunNum.node.parent = this.node;
        }
    }

    private _curNum:number=1;
    public get curNum() : number {
        return this._curNum;
    }
    public set curNum(v : number) {
        wildbandito.main.audio.prizeMulti(v-1);
        this.blurBn = Math.abs(v-this._curNum)>1;
        this._curNum = v;
        tween(this.gunScroll.node)
            .to(1,{angle:(this._curNum-1)*this.oneAngle})
            .call(()=>{
                this.blurBn = false;
            })
            .start();
    }
}