import { SpriteFrame } from 'cc';
import { Sprite } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { wildbanditoGun } from './wildbanditoGun';
import { wildbandito } from './cWildbandito';
import { Label } from 'cc';
import { Color } from 'cc';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { macro } from 'cc';
import { Prefab } from 'cc';
import { instantiate } from 'cc';
import { Num } from '../../core_tgx/framework/util/Num';
import { Animation } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('wildbanditoInfoBoard')
export class wildbanditoInfoBoard extends Component {
    @property({ type: [Node], tooltip: "底板背景" })
    public bgs: Node[] = [];
    @property({ type: [Node], tooltip: "左挡板" })
    public LMasks: Node[] = [];
    @property({ type: [Node], tooltip: "右挡板" })
    public RMasks: Node[] = [];
    @property({ type: Node, tooltip: "消息跑马灯" })
    public infos: Node = null!;
    @property({ type: Node, tooltip: "赢钱信息" })
    public infoWin: Node = null!;
    @property({ type: Animation, tooltip: "彩带动画A" })
    public ambientA: Animation = null!;
    @property({ type: Animation, tooltip: "彩带动画B" })
    public ambientB: Animation = null!;
    @property({ type: Node, tooltip: "光" })
    public light: Node = null!;
    @property({ type: Node, tooltip: "" })
    public bgBGun: Node = null!;

    onLoad() {
        wildbandito.infoBoard = this;
    }

    protected start(): void {
        this.light.active = false;
    }

    private _lev: number = 0;
    public get lev(): number {
        return this._lev
    }
    public set lev(v: number) {
        this._lev = v;
        this.bgs[0].active = v == 0;
        this.bgs[1].active = v == 1;
        this.bgs[2].active = v == 2;
        this.LMasks[0].active = v == 0;
        this.LMasks[1].active = v == 1;
        this.LMasks[2].active = v == 2;
        this.RMasks[0].active = v == 0;
        this.RMasks[1].active = v == 1;
        this.RMasks[2].active = v == 2;
        this.bgs[0].getChildByName("eyeL").active = false;
        this.bgs[0].getChildByName("eyeR").active = false;
        this.bgs[1].getChildByName("eyeL").active = false;
        this.bgs[1].getChildByName("eyeR").active = false;
        this.bgBGun.active = this.bgs[1].active;
    }

    /**winNode label */
    playAnimWin(coin: number, showTwBn: boolean = false, playBn: boolean = true) {
        if (coin == 0) return;
        this.showInfosBn = false;
        this.showTwBn = showTwBn;
        if (coin > wildbandito.betSum * 3 && coin < wildbandito.betSum * 5) {
            this.lev = 1;
        } else if (coin >= wildbandito.betSum * 5) {
            this.lev = 2;
        } else {
            this.lev = 0;
        }
        this.getWinLab().string = Num.exchangeToThousands(wildbandito.exchangeRate, coin);
        if (playBn) {
            this.playAni();
        }
    }

    private playAni() {
        if (this.lev == 2) return;
        this.light.active = true;
        this.scheduleOnce(() => {
            tween(this.light)
                .to(0.5, { scale: new Vec3(0.1, 0.1), opacity: 0 })
                .call(() => {
                    this.light.active = false;
                    this.light.opacity = 255;
                    this.light.scale = new Vec3(1, 1);
                })
                .start();
        }, 0.5)
        tween(this.infoWin)
            .to(0.2, { scale: new Vec3(1.2, 1.2) })
            .to(0.2, { scale: new Vec3(1, 1) })
            .start();
        if (this.lev == 0) {
            this.bgs[0].getChildByName("eyeL").active = true;
            this.bgs[0].getChildByName("eyeR").active = true;
            this.scheduleOnce(() => {
                tween(this.bgs[0].getChildByName("eyeL"))
                    .to(0.5, { opacity: 0 })
                    .call(() => {
                        this.bgs[0].getChildByName("eyeL").active = false;
                        this.bgs[0].getChildByName("eyeL").opacity = 255;
                    })
                    .start();
                tween(this.bgs[0].getChildByName("eyeR"))
                    .to(0.5, { opacity: 0 })
                    .call(() => {
                        this.bgs[0].getChildByName("eyeR").active = false;
                        this.bgs[0].getChildByName("eyeR").opacity = 255;
                    })
                    .start();
            }, 1)
            this.ambientA.play();
        }
        if (this.lev == 1) {
            this.bgs[1].getChildByName("eyeL").active = true;
            this.bgs[1].getChildByName("eyeR").active = true;
            this.scheduleOnce(() => {
                tween(this.bgs[1].getChildByName("eyeL"))
                    .to(0.5, { opacity: 0 })
                    .call(() => {
                        this.bgs[1].getChildByName("eyeL").active = false;
                        this.bgs[1].getChildByName("eyeL").opacity = 255;
                    })
                    .start();
                tween(this.bgs[1].getChildByName("eyeR"))
                    .to(0.5, { opacity: 0 })
                    .call(() => {
                        this.bgs[1].getChildByName("eyeR").active = false;
                        this.bgs[1].getChildByName("eyeR").opacity = 255;
                    })
                    .start();
            }, 1)
            this.ambientB.play();
            tween(this.bgBGun.children[0])
                .to(0.2, { angle: 10 })
                .to(0.2, { angle: 0 })
                .start();
            tween(this.bgBGun.children[1])
                .to(0.2, { angle: -10 })
                .to(0.2, { angle: 0 })
                .start();
        }
    }

    /**是否显示跑马灯 */
    private _showInfosBn: boolean = false;
    public get showInfosBn(): boolean {
        return this._showInfosBn
    }
    public set showInfosBn(v: boolean) {
        if (this._showInfosBn != v && v) {
            this.lev = 0;
        }
        this._showInfosBn = v;
        this.infos.active = v;
        this.infoWin.active = !v;
    }

    /**是否显示共赢得 */
    private _showTwBn: boolean = false;
    public get showTwBn(): boolean {
        return this._showTwBn;
    }
    public set showTwBn(v: boolean) {
        this._showTwBn = v;
        this.infoWin.getChildByName("info_win").active = !v;
        this.infoWin.getChildByName("info_tw").active = v;
    }

    private getWinLab(): Label {
        return this.infoWin.getChildByName("Label").getComponent(Label);
    }
}