import { SpriteFrame } from 'cc';
import { Sprite } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { wildbanditoGun } from './wildbanditoGun';
import { wildbandito } from './cWildbandito';
import { Label } from 'cc';
import { Color } from 'cc';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { macro } from 'cc';
import { Prefab } from 'cc';
import { instantiate } from 'cc';
import { Num } from '../../core_tgx/framework/util/Num';
import { Animation } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('wildbanditoFreeStart')
export class wildbanditoFreeStart extends Component {

    @property({ type: Label, tooltip: "免费次数" })
    public count: Label = null!;
    @property({ type: Node, tooltip: "免费次数" })
    public man: Node = null!;
    @property({ type: Node, tooltip: "黄光" })
    public yellowLight: Node = null!;
    @property({ type: Label, tooltip: "" })
    public progress: Label = null!;
    @property({ type: Node, tooltip: "" })
    public btn: Node = null!;
    onLoad() {
        this.showBtnBn = false;
        this.progress.string = "0";
    }

    protected start(): void {
       
    }

    doProgress(){
        tween(this.progress)
            .to(1,{string:"100"})
            .call(()=>{
                this.showBtnBn = true;
            })
            .start();
    }

    setParent() {
        wildbandito.main.audio.playFsLoadingVox();
        this.node.parent = wildbandito.slotCtrl.node;
        this.node.setSiblingIndex(this.node.parent.children.length - 2);
        this.doProgress();
        let initManx:number = this.man.x;
        this.man.x -=100;
        this.man.opacity = 0;
        tween(this.man)
            .to(0.5,{opacity:255,x:initManx})
            .by(5,{scale:new Vec3(0.2,0.2),position:new Vec3(20,20)})
            .start();
        this.yellowLight.active = true;
        tween(this.yellowLight)
            .to(0.5,{opacity:0})
            .start();
        this.scheduleOnce(this.close,5);
        this.count.string = 12+"";
        wildbandito.main.bg.fortune = true;
        wildbandito.main.bg.freeTimes = wildbandito.round.freeCount;
    }

    /**是否显示按钮 */
    private _showBtnBn: boolean = false;
    public get showBtnBn(): boolean {
        return this._showBtnBn;
    }
    public set showBtnBn(v: boolean) {
        if(v && v!=this._showBtnBn){
            this.btn.active = true;
            this.btn.scale = new Vec3(0,0);
            tween(this.btn)
                .to(0.5,{scale:new Vec3(1.3,1.3)})
                .start();
        }
        this._showBtnBn = v;
        this.progress.node.active = !v;
        this.btn.active = v;
        if (v && this.node.parent) {
            this.node.setSiblingIndex(this.node.parent.children.length - 1);
        }
    }

    private close(){
        this.node.parent = null;
        wildbandito.main.freeStartBack();
    }

    private onClose(){
        if(this.showBtnBn){
            this.close();
            wildbandito.main.audio.playFswonIncrease();
        }
    }
}