

import { Node } from "cc";
import { SubGameDef } from "../../module_basic/scripts/SubGameDef";
import { wildbanditoSymbol } from "./wildbanditoSymbol";
import { wildbanditoBigwin } from "./wildbanditoBigwin";
import { wildbanditoMain } from "./wildbanditoMain";
import { wildbanditoSlotCtrl } from "./wildbanditoSlotCtrl";
import { wildbanditoInfoBoard } from "./wildbanditoInfoBoard";
import { wildbanditoReel } from "./wildbanditoReel";
import { clientRes, dataChange, Result, Round } from "./dataChange";
import { wildbanditoDetail } from "./wildbanditoDetail";
import { betPanel } from "./betPanel";
import { autoPanel } from "./autoPanel";
import { lastmoneyPanel } from "./lastmoneyPanel";


class cWildbandito {
    public static _instance: cWildbandito | null = null;

    public static get instance(): cWildbandito {
        if (!this._instance) {
            this._instance = new cWildbandito();
        }
        return this._instance;
    }

    public BETNUM = dataChange.BETNUM;
    public LINES = dataChange.LINES;
    public BET = dataChange.BET;

    private _isNewUser: boolean = false;
    public get isNewUser(): boolean {
        this._isNewUser = false;
        return this._isNewUser;
    }

    private _money: number = 0;
    public get money(): number {
        return this._money;
    }
    public set money(m: number) {
        this._money = m;
    }

    private _exchangeRate: number = 0;
    public get exchangeRate(): number {
        this._exchangeRate = 1;
        return this._exchangeRate;
    }

    public detail: wildbanditoDetail = null!;
    public main: wildbanditoMain = null!;
    public slotCtrl: wildbanditoSlotCtrl = null!;
    public infoBoard: wildbanditoInfoBoard = null!;
    public reel: wildbanditoReel;
    public bet: number = 0;
    public betNum: number = 0;
    public sumIdx: number = 0;

    /**累计下注计算 */
    private _betSum: number;

    public get betSum(): number {
        if (!this._betSum) {
            this._betSum = Number(localStorage.getItem(SubGameDef.SLOTS_DRAGONHATCH + "betSum"));
        }
        return this._betSum;
    }

    public set betSum(v: number) {
        if (this._betSum != v) {
            localStorage.setItem(SubGameDef.SLOTS_DRAGONHATCH + "betSum", v + "");
        }
        this._betSum = v;
    }

    /**获奖信息 */
    public lotteryRes: clientRes = null!;

    /**自动状态 */
    public auto: boolean = false;
    /**自动次数 */
    public autoTimes: number = 0;
    /**转动状态0：停止，1：转动，2：开始转 */
    public status: number = 0;
    /**中奖数字位置 */
    public winLinePos: number[] = [];
    /**大奖个数 */
    public bigWinNum: number = 0;
    /**摇奖计数 */
    public rollIndex: number = 0;
    public _rollEnable: boolean = true;

    /**bigwin*/
    public bigwin: wildbanditoBigwin = null!;
    /**下注界面 */
    public betPanel: betPanel = null!;
    /**自动界面 */
    public autoPanel: autoPanel = null!;
    /**余额界面 */
    public lastmoneyPanel: lastmoneyPanel = null!;

    public set rollEnable(v: boolean) {
        this._rollEnable = v;
    }

    public get rollEnable(): boolean {
        return this._rollEnable;
    }
    /**摇奖次数index */
    private _resultIndex: number = 0;
    public get resultIndex(): number {
        return this._resultIndex
    }
    public set resultIndex(v: number) {
        this._resultIndex = v;
    }
    /**回合index */
    private _roundIndex: number = 0;
    public get roundIndex(): number {
        return this._roundIndex
    }
    public set roundIndex(v: number) {
        this._roundIndex = v;
    }
    /**摇奖次数数据 */
    public get result(): Result {
        return this.lotteryRes.results[this.resultIndex];
    }
    /**回合数据 */
    public get round(): Round {
        return this.result.rounds[this.roundIndex];
    }
    /**播放结束 */
    public get playOverBn(): boolean {
        return this.resultIndex > this.lotteryRes.results.length - 1;
    }
    /**是否展示免费模式滚动开始动画 */
    public get showFreeLoopAni(): boolean {
        if (this.resultIndex == 1) return true;
        if (this.resultIndex > 1 && this.lotteryRes.results[this.resultIndex - 1].winCoin > 0) return true;
        return false;
    }

    public removeCount: number;

    constructor() {

    }
}

export let wildbandito = cWildbandito.instance;