import { SpriteFrame } from 'cc';
import { Sprite } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { wildbanditoGun } from './wildbanditoGun';
import { wildbandito } from './cWildbandito';
import { Label } from 'cc';
import { Color } from 'cc';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { macro } from 'cc';
import { Prefab } from 'cc';
import { instantiate } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('wildbanditoBg')
export class wildbanditoBg extends Component {
    @property({ type: Sprite, tooltip: "顶部背景" })
    public top: Sprite = null!;
    @property({ type: [SpriteFrame], tooltip: "顶部背景" })
    public tops: SpriteFrame[] = [];
    @property({ type: Sprite, tooltip: "桌面背景" })
    public desk: Sprite = null!;
    @property({ type: [SpriteFrame], tooltip: "桌面背景" })
    public desks: SpriteFrame[] = [];
    @property({ type: wildbanditoGun, tooltip: "手枪" })
    public gun: wildbanditoGun = null!;
    @property({ type: Node, tooltip: "蜡烛" })
    public candle: Node = null!;
    @property({ type: Sprite, tooltip: "底部背景" })
    public downBg: Sprite = null!;
    @property({ type: [SpriteFrame], tooltip: "底部背景" })
    public downBgs: SpriteFrame[] = [];
    @property({ type: Prefab, tooltip: "彩带" })
    public ambient: Prefab = null!;
    onLoad() {

    }

    protected start(): void {
        this.createAmbient();
    }

    private init() {
    }

    /**辛运模式 */
    private _fortune: boolean;
    public get fortune(): boolean {
        return this._fortune;
    }
    public set fortune(v: boolean) {
        if (v) {
            wildbandito.main.audio.playBgm(1)
        }else{
            wildbandito.main.audio.playBgm(0)
        }
        this._fortune = v;
        this.gun.fortune = v;
        if (v) {
            this.top.node.y = 250;
            this.top.spriteFrame = this.tops[1];
            this.desk.spriteFrame = this.desks[1];
            this.downBg.spriteFrame = this.downBgs[1];
            wildbandito.slotCtrl.coin_panel.y = -644;
        }
        else {
            this.top.node.y = 325;
            this.top.spriteFrame = this.tops[0];
            this.desk.spriteFrame = this.desks[0];
            this.downBg.spriteFrame = this.downBgs[0];
            wildbandito.slotCtrl.coin_panel.y = -402;
        }
        this.candle.active = v;
        wildbandito.slotCtrl.betting_panel.active = !v;
    }

    private _freeTimes: number;

    public get freeTimes(): number {
        return this._freeTimes
    }

    public set freeTimes(v: number) {
        this._freeTimes = v;
        let freeNode: Node = this.downBg.node.getChildByName("free");
        freeNode.active = v >= 0;
        let numNode: Node = freeNode.getChildByName("num");
        numNode.active = v > 0;
        numNode.getComponent(Label).string = v + "";
        let lastNode: Node = freeNode.getChildByName("last");
        lastNode.active = v == 0;
    }

    private createAmbient() {
        this.schedule(this.ambientFactory, 0.5, macro.REPEAT_FOREVER);
    }
    private colors: string[] = ["#fcfc4d", "#e859de", "#eb2c4c", "#f244e1"];
    /**彩带工厂 自产自销*/
    private ambientFactory() {
        let sp: Sprite = instantiate(this.ambient).getComponent(Sprite);
        sp.node.parent = this.node.parent;
        sp.node.setSiblingIndex(sp.node.parent.children.length - 2);
        let index: number = Math.floor(this.colors.length * Math.random());
        if (index == this.colors.length) {
            index = this.colors.length - 1;
        }
        sp.color = new Color(this.colors[index]);
        sp.node.angle = 360 * Math.random();
        let startPos: Vec3 = new Vec3();
        startPos.x = -600 + Math.random() * 900;
        startPos.y = 900;
        let endPos: Vec3 = new Vec3();
        endPos.x = startPos.x + 500 + 200 * Math.random();
        endPos.y = -900;
        sp.node.position = startPos;
        tween(sp.node)
            .to(3 + 5 * Math.random(), { position: endPos })
            .call(() => {
                sp.node.destroy();
            })
            .start();
    }
}